<template>
    <div>
        <div v-show="!isScanCodeDetail">
            <el-card class="box-card">
                <!--  -->
                <el-form :inline="true" :model="form">
                    <el-form-item label="user_id">
                        <el-input size="small" v-model="form.userid"></el-input>
                    </el-form-item>
                    <el-form-item label="SN">
                        <el-input size="small" v-model="form.productSn"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <div class="block">
                            <el-date-picker size="small" type="date" v-model="form.date" placeholder="" :clearable="false" :editable="false"></el-date-picker>
                            <el-time-picker size="small" is-range v-model="form.time" :clearable="false"  :editable="false"
                                value-format="HH:mm:ss"
                                range-separator="" 
                                start-placeholder="" 
                                end-placeholder="" 
                                placeholder="">
                            </el-time-picker>
                        </div>
                    </el-form-item>
                    <div></div>
                    <el-form-item>
                        <el-button size="small" type="primary" @click="list"></el-button>
                        <el-button size="small" @click="resetForm"></el-button>
                    </el-form-item>
                </el-form>
            </el-card>
            <div class="space"></div>
            <el-card class="box-card">
                <!--  -->
                <el-table v-loading="loading" :data="tableList" style="width: 100%">
                    <el-table-column label="" align="center">
                        <template slot-scope="scope">
                            <el-button size="mini" @click="scanCodeDetail(scope.$index, scope.row)">CODE</el-button>
                        </template>
                    </el-table-column>
                    <!--
                    <el-table-column label="" type="index" align="center" width="60">
                        <template slot-scope="scope">
                            {{ (form.pageNo-1)*form.pageSize + scope.$index + 1 }}
                        </template>
                    </el-table-column>
                    -->
                    <el-table-column prop="code" label="CODE" align="center"></el-table-column>
                    <el-table-column prop="memo" label="" align="center"></el-table-column>
                    <el-table-column prop="userid" label="ID" align="center"></el-table-column>
                    <el-table-column prop="productSn" label="SN" align="center"></el-table-column>
                    <el-table-column prop="ctime" label="" align="center">
                        <template slot-scope="scope">
                            <span v-if="scope.row.ctime">{{new Date(scope.row.ctime).Format("yyyy-MM-dd hh:mm:ss")}}</span>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="space"></div>
                <!--  -->
                <!--
                <el-pagination background layout="total, prev, pager, next, jumper"
                    @size-change="sizeChange" @current-change="currentChange"
                    :current-page="form.pageNo" :page-size="form.pageSize"
                    :total="pagination.total">
                </el-pagination>
                -->
            </el-card>
        </div>
        <div v-show="isScanCodeDetail">
            <el-card class="box-card">
                <div class="scancode-detail-header">
                    <span>{{detailForm.code}} </span>
                    <a href="javascript:;" @click="goSearch"></a>
                </div>
                <div class="space"></div>
                <!--  -->
                <el-table v-loading="loading" :data="detailTable" style="width: 100%">
                    <el-table-column label="" type="index" align="center">
                        <template slot-scope="scope">
                            {{ (detailForm.page-1)*detailForm.pageSize + scope.$index + 1 }}
                        </template>
                    </el-table-column>
                    <el-table-column prop="mobile" label="" align="center" width="120"></el-table-column>
                    <el-table-column prop="region" label="" align="center"></el-table-column>
                    <el-table-column prop="city" label="" align="center"></el-table-column>
                    <el-table-column prop="batch" label="" align="center"></el-table-column>
                    <el-table-column prop="productSn" label="SN" align="center"></el-table-column>
                    <el-table-column prop="ctime" label="" align="center">
                        <template slot-scope="scope">
                            <span v-if="scope.row.ctime">{{new Date(scope.row.ctime).Format("yyyy-MM-dd hh:mm:ss")}}</span>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="space"></div>
                <!--  -->
                <el-pagination background layout="total, prev, pager, next, jumper"
                    @size-change="sizeChange" @current-change="currentChange"
                    :current-page="detailForm.page" :page-size="detailForm.pageSize"
                    :total="pagination.total">
                </el-pagination>
            </el-card>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            isScanCodeDetail: false, // 
            loading: false, // 
            pickerOptions: {
                disabledDate(time) {
                    return time.getTime() >= Date.now();
                }
            },
            form: { // 
                //pageNo: 1,
                //pageSize: 10,
                userid: "",
                productSn: "",
                date: "",
                time: ["00:00:00", "00:59:59"], // 
                beginTime: "", // --
                endTime: "" // --
            },
            tableList: [],

            detailForm: {
                page: 1,
                pageSize: 10,
                code: ""
            },
            detailTable: [],
            pagination: { // 
                total: 0
            }
        }
    },
    created() {
        this.initDate();
    },
    methods: {
        currentChange(pageNo) {
            // pageNo
            this.list(event, pageNo);
        },
        sizeChange(pageSize) {
            // pageSize
            this.list(event, null, pageSize);
        },
        initDate() {
            let _now = new Date();
            this.form.date = _now.Format("yyyy-MM-dd");
            this.form.time = ["00:00:00", "00:59:59"];
        },
        // 
        resetForm() {
            this.form = { // 
                //pageNo: 1,
                //pageSize: 10,
                userid: "",
                productSn: "",
                date: "",
                time: [], // 
                beginTime: "", // -- ::
                endTime: "" // -- ::
            };
            this.initDate();
            this.tableList = []
        },
        // 
        initPagination(page) {
            if(page) {
                this.pagination.total = page.count;
            }
        },
        // 
        list() {
            if( !this.form.userid ) {
                this.$message.error("user_id");
                return false;
            }
            if( !this.form.productSn ) {
                this.$message.error("SN");
                return false;
            }

            this.loading = true;

            this.form.beginTime = this.form.date + " " + this.form.time[0];
            this.form.endTime = this.form.date + " " + this.form.time[1];

            // 
            this.$request.post('/record/fixatreport/getDetailHome', this.form, true, (res)=>{
                this.loading = false;
                this.tableList = res || [];
                //this.tableList = [{code:"123",memo:"",userid:"12312312312312312312312312312312",productSn:"123123123123",ctime:""}];
            });
        },
        goSearch() {
            this.detailForm.code = "";
            this.isScanCodeDetail = false;
        },
        // CODE
        scanCodeDetail(index, row) {
            if( row.code ) {
                this.detailForm.code = row.code;
                this.listScanCodeDetail();
            }
        },
        listScanCodeDetail() {
            this.loading = true;
            // 
            this.$request.post('/record/fixatreport/getDetailByCodeId', this.detailForm, true, (res)=>{
                this.loading = false;
                //res = {Count:1, data:[{mobile:"12345678901",region:"",city:"",batch:"2018011022112",productSn:"1234567890123",ctime:""}]};
                this.detailTable = res.data || [];
                let page = {count:res.Count};
                this.initPagination(page);
                this.isScanCodeDetail = true;
            });
        }
    }
}
</script>

<style scoped>
    .space {position:relative;width:100%;height:20px;}
    .el-input, .el-select {
        width: 200px;
    }
    .scancode-detail-header {
        position: relative;
        padding: 0 20px;
        height: 30px;
        line-height: 30px;
        color: #606266;
    }
    .scancode-detail-header a {
        position: absolute;
        right: 30px;
    }
</style>



