<template>
  <div>
    <el-card class="box-card">
        <!--  -->
        <el-form :inline="true" :model="form">
            <el-form-item label="">
                <el-input size="small" v-model="form.activityName"></el-input>
            </el-form-item>
            <el-form-item label="" class="ml15">
                <el-select size="small" v-model="form.productBrand" placeholder="" @change="brandChange">
                    <el-option label="" value=""></el-option>
                    <el-option v-for="item in brandList" :key="item.brandCode" :label="item.name" :value="item.name"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-select size="small" v-model="form.productSn" placeholder="">
                    <el-option label="" value=""></el-option>
                    <el-option v-for="item in snList" :key="item.sn" :label="item.name" :value="item.sn"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-select size="small" v-model="form.awardType" :default-first-option="true">
                    <el-option v-for="item in awardList" :key="item.awardTypeId" :label="item.awardTypeName" :value="item.awardTypeId"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-select size="small" v-model="form.checkStatus" placeholder="">
                    <el-option label="" value=""></el-option>
                    <el-option v-for="(label,code) in visitLabels" :key="code" :label="label" :value="code"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-input size="small" v-model="form.checkPerson"></el-input>
            </el-form-item>
            <div></div>
            <el-form-item>
                <el-button size="small" type="primary" @click="list"></el-button>
                <el-button size="small" @click="resetForm"></el-button>
            </el-form-item>
        </el-form>
    </el-card>
    <div class="space"></div>
    <el-card class="box-card">
        <!--  -->
        <el-table v-loading="loading" :data="tableList" style="width: 100%">
            <el-table-column label="" type="index" align="center" width="60">
                <template slot-scope="scope">
                    {{ (form.page-1)*form.pageSize + scope.$index + 1 }}
                </template>
            </el-table-column>
            <el-table-column prop="orderId" label="" align="center" width="300"></el-table-column>
            <el-table-column prop="activityName" label="" align="center" width="200"></el-table-column>
            <el-table-column prop="productName" label="" align="center" width="150"></el-table-column>
            <el-table-column prop="awardType" label="" align="center" width="80"></el-table-column>
            <el-table-column prop="awardName" label="" align="center" width="120"></el-table-column>
            <el-table-column prop="awardCnt" label="" align="center" width="50"></el-table-column>
            <el-table-column prop="awardFee" label="" align="center" width="100"></el-table-column>
            <el-table-column prop="userId" label="ID" align="center" width="280"></el-table-column>
            <el-table-column prop="mobileNo" label="" align="center" width="120"></el-table-column>
            <el-table-column prop="openId" label="ID" align="center" width="280"></el-table-column>
            <el-table-column prop="cityName" label="" align="center" width="130"></el-table-column>
            <el-table-column prop="acceptTime" label="" align="center" width="160"></el-table-column>
            <el-table-column prop="orderStatus" label="" align="center" width="120"></el-table-column>
            <el-table-column prop="checkStatus" label="" align="center" width="100">
                <template slot-scope="scope">
                    {{visitLabels[scope.row.checkStatus]}}
                </template>
            </el-table-column>
            <el-table-column prop="checkTime" label="" align="center" width="160"></el-table-column>
            <el-table-column prop="checkPerson" label="" align="center" width="150"></el-table-column>
        </el-table>
        <div class="space"></div>
        <!--  -->
        <el-pagination background layout="total, prev, pager, next, jumper"
            @size-change="sizeChange" @current-change="currentChange"
            :current-page="form.pageNo" :page-size="form.pageSize"
            :total="pagination.total">
        </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
    data() {
        return {
            visitLabels: { // 
                "1": "",
                "2": "",
                "3": "",
                "4": ""
            },
            loading: false, // 
            brandList: [], // 
            snList: [], // 
            awardList: [], // 
            pickerOptions: {
                disabledDate(time) {
                    return time.getTime() >= Date.now();
                },
                // selectDate = { maxDate, minDate }
                onPick(selectDate) {
                    //console.log(selectDate.minDate);
                    //console.log(selectDate.maxDate);
                }
            },
            form: { // 
                page: 1,
                pageSize: 10, 
                activityName: "",
                awardType: "",
                productBrand: "",
                productSn: "",
                checkStatus: "",
                checkPerson: ""
            },
            tableList: [],
            pagination: { // 
                total: 0
            }
        }
    },
    created() {
        // const cluserStr = sessionStorage.getItem("cluser");
        // this.cluser = JSON.parse(cluserStr);
        this.cluser = this.$store.state.cluser
        if(!this.cluser||!this.cluser.account) {
            this.clearLoginSession();
            return false;
        }
        this.getBrandList();
        this.getSnList();
        this.getAwardList();// 
    },
    methods: {
        // 
        clearLoginSession() {
            // sessionStorage.removeItem("cluser");
            sessionStorage.removeItem("access_token");
            sessionStorage.removeItem("access_loginId");
        },
        currentChange(pageNo) {
            this.form.page = pageNo;
            // pageNo
            this.list();
        },
        sizeChange(pageSize) {
            // pageSize
            this.form.pageSize = pageSize;
        },
        // 
        getBrandList() {
            this.$request.post('/record/public/getUserBrandByUserId', {}, true, (res)=>{
                this.brandList = res||[];
            });
        },
        // 
        brandChange() {
            this.getSnList();
        },
        getSnList() {
            this.$request.post('/record/statistics/getProduct', {productBrand: this.form.productBrand}, true, (res)=>{
                this.snList = res||[];
            });
        },
        getAwardList() {
            this.$request.post('/record/statistics/getAwardType', {}, true, (res)=>{
                this.awardList = res||[];
                if(this.awardList.length>0) {
                    this.form.awardType = this.awardList[0].awardTypeId;
                }
                this.list();
            });
        },
        // 
        resetForm() {
            this.form = { // 
                page: 1,
                pageSize: 10, 
                activityName: "",
                awardType: "",
                productBrand: "",
                productSn: "",
                checkStatus: "",
                checkPerson: ""
            };
            this.list();
        },
        // 
        initPagination(page) {
            if(page) {
                this.pagination.total = page.Count;
            }
        },
        // 
        list() {
            this.loading = true;
            // 
            this.$request.post('/record/statistics/getPerformAppoint', this.form, true, (res)=>{
                this.loading = false;
                this.tableList = res || [];
            });
            // 
            this.$request.post('/record/statistics/getPerformAppointCount', this.form, true, (res)=>{
                let page = res||{}; // {Count: 0}
                this.initPagination(page);
            });
        }
    }
}
</script>

<style scoped>
    .ml15 {margin-left:15px;}
    .space {position:relative;width:100%;height:20px;}
    .el-input, .el-select {
        width: 200px;
    }
</style>



