<template>
  <div>
    <el-card class="box-card">
        <!--  -->
        <el-form :inline="true" :model="form">
            <el-form-item label="">
                <el-input size="small" v-model="form.awardName"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input size="small" v-model="form.cityName"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input size="small" v-model="form.mobileNo"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input size="small" v-model="form.orderId"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-select size="small" v-model="form.productBrand" placeholder="" @change="brandChange">
                    <el-option label="" value=""></el-option>
                    <el-option v-for="item in brandList" :key="item.brandCode" :label="item.name" :value="item.name"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-select size="small" v-model="form.productSn" placeholder="">
                    <el-option label="" value=""></el-option>
                    <el-option v-for="item in snList" :key="item.sn" :label="item.name" :value="item.sn"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" class="ml15">
                <el-select size="small" v-model="form.checkStatus" placeholder="">
                    <el-option label="" value=""></el-option>
                    <el-option v-for="(label,code) in visitLabels" :key="code" :label="label" :value="code"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <div class="block">
                    <el-date-picker size="small"
                        v-model="form.time"
                        type="daterange"
                        range-separator=""
                        start-placeholder=""
                        end-placeholder=""
                        :picker-options="pickerOptions">
                    </el-date-picker>
                </div>
            </el-form-item>
            <div></div>
            <el-form-item>
                <el-button size="small" type="primary" @click="list"></el-button>
                <el-button size="small" @click="resetForm"></el-button>
            </el-form-item>
        </el-form>
    </el-card>
    <div class="space"></div>
    <el-card class="box-card">
        <!--  -->
        <el-table v-loading="loading" :data="tableList" style="width: 100%">
            <el-table-column label="" type="index" align="center" width="60">
                <template slot-scope="scope">
                    {{ (form.page-1)*form.pageSize + scope.$index + 1 }}
                </template>
            </el-table-column>
            <el-table-column prop="mobile" label="" align="center" width="120"></el-table-column>
            <el-table-column prop="orderId" label="" align="center" width="300"></el-table-column>
            <el-table-column prop="wechatName" label="" align="center" ></el-table-column>
            <el-table-column prop="statTime" label="" align="center" width="160"></el-table-column>
            <el-table-column prop="cityName" label="" align="center" width="130"></el-table-column>
            <el-table-column prop="awardName" label="" align="center" width="120"></el-table-column>
            <el-table-column label="" align="center" >
                <template slot-scope="scope">
                    {{visitLabels[scope.row.feedbackStatus]}}
                    <el-dropdown v-if="scope.row.feedbackStatus==1" trigger="click" :hide-on-click="false" @command="handleCommand">
                        <span class="el-dropdown-link">
                            <i class="el-icon-arrow-down el-icon--right"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item :command="scope.row.id+',2'"></el-dropdown-item>
                            <el-dropdown-item :command="scope.row.id+',3'"></el-dropdown-item>
                            <el-dropdown-item :command="scope.row.id+',4'"></el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </template>
            </el-table-column>
        </el-table>
        <div class="space"></div>
        <!--  -->
        <el-pagination background layout="total, prev, pager, next, jumper"
            @size-change="sizeChange" @current-change="currentChange"
            :current-page="form.pageNo" :page-size="form.pageSize"
            :total="pagination.total">
        </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
    data() {
        return {
            visitLabels: { // 
                "1": "",
                "2": "",
                "3": "",
                "4": ""
            },
            loading: false, // 
            brandList: [], // 
            snList: [], // 
            pickerOptions: {
                disabledDate(time) {
                    return time.getTime() >= Date.now();
                },
                // selectDate = { maxDate, minDate }
                onPick(selectDate) {
                    //console.log(selectDate.minDate);
                    //console.log(selectDate.maxDate);
                }
            },
            yesterday: "",
            form: { // 
                page: 1,
                pageSize: 10, 
                awardName: "",
                cityName: "",
                mobileNo: "",
                orderId: "",
                productBrand: "",
                productSn: "",
                checkStatus: "",
                time: [], // 
                startTime: "", // --
                endTime: "" // --
            },
            tableList: [],
            pagination: { // 
                total: 0
            },
            cluser:{},
        }
    },
    created() {
        // const cluserStr = sessionStorage.getItem("cluser");
        const cluserStr = this.$store.state.cluser
        this.cluser = cluserStr;
        // if(!this.cluser||!this.cluser.account) {
        //     this.clearLoginSession();
        //     return false;
        // }
        let _now = new Date();
        _now.setTime(_now.getTime()-24*60*60*1000);
        this.yesterday = _now.getFullYear()+"-" + (_now.getMonth()+1) + "-" + _now.getDate();
        this.form.time = [this.yesterday, this.yesterday];
        this.getBrandList();
        this.getSnList();
        this.list();
    },
    methods: {
        // 
        clearLoginSession() {
            // sessionStorage.removeItem("cluser");
            sessionStorage.removeItem("access_token");
            sessionStorage.removeItem("access_loginId");
        },
        currentChange(pageNo) {
            // pageNo
            this.list(event, pageNo);
        },
        sizeChange(pageSize) {
            // pageSize
            this.list(event, null, pageSize);
        },
        // 
        getBrandList() {
            this.$request.post('/record/public/getUserBrandByUserId', {}, true, (res)=>{
                this.brandList = res||[];
            });
        },
        // 
        brandChange() {
            this.getSnList();
        },
        getSnList() {
            this.$request.post('/record/statistics/getProduct', {productBrand: this.form.productBrand}, true, (res)=>{
                this.snList = res||[];
            });
        },
        // 
        resetForm() {
            this.form = { // 
                page: 1,
                pageSize: 10, 
                awardName: "",
                cityName: "",
                mobileNo: "",
                orderId: "",
                productBrand: "",
                productSn: "",
                checkStatus: "",
                time: [this.yesterday, this.yesterday], // 
                startTime: "", // --
                endTime: "" // --
            };
            this.list();
        },
        // 
        initPagination(page) {
            if(page) {
                this.pagination.total = page.Count;
            }
        },
        // 
        list(_event, pageNo, pageSize) {
            this.loading = true;
            if(pageNo) {
                this.form.pageNo = pageNo;
            } else {
                this.form.pageNo = 1;
            }
            if(pageSize) {
                this.form.pageSize = pageSize;
            } else {
                this.form.pageSize = 10;
            }
            if(this.form.time&&this.form.time.length==2) {
                this.form.startTime = new Date(this.form.time[0]).Format("yyyy-MM-dd");
                this.form.endTime = new Date(this.form.time[1]).Format("yyyy-MM-dd");
            } else {
                this.form.startTime = this.form.endTime = "";
            }
            // 
            this.$request.post('/record/statistics/getFeedBack', this.form, true, (res)=>{
                this.loading = false;
                this.tableList = res || [];
            });
            // 
            this.$request.post('/record/statistics/feedbackCount', this.form, true, (res)=>{
                let page = res||{}; // {Count: 0}
                this.initPagination(page);
            });
        },
        handleCommand(command) {
            if(!command) return false;

            let id = command.split(",")[0];
            let idCheckStatus = command.split(",")[1];
            this.$confirm('<span style="color:red">()</span>', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning',
                dangerouslyUseHTMLString: true,
                center: true
            }).then(() => {
                // 
                // {id: "28118890", checkStatus: 2, checkPerson: "hebei_cuiguochao", checkTime: "2018-10-24 15:21:08"}
                let updateForm = {id:id, checkStatus:idCheckStatus, checkTime:new Date().Format("yyyy-MM-dd hh:mm:ss"), checkPerson: this.cluser.name};
                this.$request.post('/record/statistics/updateFeddBackStatus', updateForm, true, (res)=>{
                    this.list();
                });
            }).catch(() => {/**  */});
        }
    }
}
</script>

<style scoped>
    .ml15 {margin-left:15px;}
    .space {position:relative;width:100%;height:20px;}
    .el-input, .el-select {
        width: 200px;
    }
</style>



