<template>
	<div class="multi-root">
		<div class="search-condition">
			<div class="title" @click='toggleShow'>&nbsp;<span :class='{"el-icon-caret-bottom":true,active:!conditionShow}'></span></div>
			<div class="condition-con" v-show='conditionShow'>
				<div class="inputs">
					
					<el-date-picker v-model="startDate"format="yyyy-MM-dd" type="date" placeholder="" size='small'@change='selectStartDay' :clearable="false">
					</el-date-picker>
					<el-time-select v-model="startTime" size='small' :picker-options="{start: '00:00',step: '01:00',end: '23:59'}" @change="selectTime" placeholder="" :clearable="false">
					</el-time-select>
					<span>~</span>
					<el-date-picker v-model="endDate" type="date" placeholder="" size='small'@change='selectEndDay' :clearable="false">
					</el-date-picker>
					<el-time-select v-model="endTime" size='small' :picker-options="{start: '00:00',step: '01:00',end: '23:59'}" @click="selectTime" placeholder="" :clearable="false">
					</el-time-select>
					<br /><br /> 
					&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
					<el-select v-model="brand" multiple filterable placeholder="" size='small'>
						<el-option v-for="item in brandList" :key="item.brandCode" :label="item.name" :value="item.name">
						</el-option>
					</el-select>
					&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
					
					<el-select v-model="spec" multiple filterable placeholder="" size='small' @focus='getSpec()'>
						<el-option v-for="item in specList" :key="item.name" :label="item.name" :value="item.name">
						</el-option>
					</el-select>
					&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
					 
					<el-select v-model="packing" multiple filterable placeholder="" size='small'>
						<el-option v-for="item in packList" :key="item.name" :label="item.name" :value="item.name">
						</el-option>
					</el-select>
					<br /><br />
					&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
					<el-select v-model="area" multiple filterable placeholder="" size='small'>
						<el-option v-for="item in areaList" :key="item.code" :label="item.name" :value="item.code">
						</el-option>
					</el-select>
					&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
					
					<el-select v-model="pro" multiple filterable placeholder="" size='small'@focus='getPro'>
						<el-option v-for="item in proList" :key="item.name" :label="item.name" :value="item.name">
						</el-option>
					</el-select>
					&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
					
					<el-select v-model="city" multiple filterable placeholder="" size='small'@focus='getCity'>
						<el-option v-for="item in cityList" :key="item.cityId" :label="item.cityName" :value="item.cityName">
						</el-option>
					</el-select>
				</div>
				<div class="search-result">
					
					<el-tag type="info">{{transformStart}}:00</el-tag>
					<el-tag type="info">{{transformEnd}}:00</el-tag>
					<el-tag type="info"v-show='brand.length!=0'closable @close="handleClose(0)">{{transformBrand}}</el-tag>
					<el-tag type="info"v-show='spec.length!=0'closable @close="handleClose(1)">{{transformSpec}}</el-tag>
					<el-tag type="info"v-show='packing.length!=0'closable @close="handleClose(2)">{{transformPack}}</el-tag>
					<el-tag type="info"v-show='area.length!=0'closable @close="handleClose(3)">{{transformAreaShow}}</el-tag>
					<el-tag type="info"v-show='pro.length!=0'closable @close="handleClose(4)">{{transformPro}}</el-tag>
					<el-tag type="info"v-show='city.length!=0'closable @close="handleClose(5)">{{transformCity}}</el-tag>
				</div>
				<div class="search-oper">
					<div class="oper-btn">
						<el-button type="primary"size='small' round @click='search'></el-button>
					</div>
					<div class="oper-btn">
						<el-button type="primary"size='small' round @click='saveTpl'></el-button>
					</div>
					<div class="oper-btn">
						<el-button type="primary"size='small' round @click="load"></el-button>
						<el-dialog title="":visible.sync="dialogTableVisible">
							<div class="tpl-con">
								<el-radio-group v-model="loadValue"@change='tplChange'>
								    <el-radio v-for='(item,key) in loadList' :label="key":key='key'>
								    	<span>{{item.startTime}}+</span>
								    	<span>{{item.endTime}}</span>
								    	<span>+{{item.productBrand}}</span>
								    	<span>+{{item.productName}}</span>
								    	<span v-show='item.productPack.length!=0'>+{{item.productPack}}</span>
								    	<span v-show='item.saleZone.length!=0'>+{{transformAreaCommon(item.saleZone)}}</span>
								    	<span>+{{item.provinceName}}</span>
								    	<span v-show='item.cityName.length!=0'>+{{item.cityName}}</span>
								    </el-radio>
							  	</el-radio-group>
							</div>
						</el-dialog>
					</div>
					<div class="oper-btn">
						<el-button type="primary"size='small' round @click="getHistory"></el-button>
						<el-dialog title="":visible.sync="dialogTableVisible1">
							<div class="tpl-con">
								<el-radio-group v-model="historyValue"@change='hisChange'>
								    <el-radio v-for='(item,key) in historyList' :label="key":key='key'>
								    	<span>{{item.startTime}}+</span>
								    	<span>{{item.endTime}}</span>
								    	<span>+{{item.productBrand}}</span>
								    	<span>+{{item.productName}}</span>
								    	<span v-show='item.productPack.length!=0'>+{{item.productPack}}</span>
								    	<span v-show='item.saleZone.length!=0'>+{{transformAreaCommon(item.saleZone)}}</span>
								    	<span>+{{item.provinceName}}</span>
								    	<span v-show='item.cityName.length!=0'>+{{item.cityName}}</span>
								    </el-radio>
							  	</el-radio-group>
							</div>
						</el-dialog>
					</div>
				</div>
			</div>
		</div>
		<div class="result-part">
			<div class="result-tab">
				<ul>
					<li v-for='(item,key) in tabList':class='{active:tabActive==key}'@click='itemSearch(key)'>{{item}}</li>
				</ul>
			</div>
			<div class="result-table">
				<el-table :data="tableData"v-loading="loading" style="width: 100%" >
						<el-table-column prop="startTime" label="" width="200">
						</el-table-column>
						<el-table-column prop="endTime" label="" width="200">
						</el-table-column>
						<el-table-column prop="productBrand" label="" width="120">
						</el-table-column>
						<el-table-column prop="productName" label="" width="150">
						</el-table-column>
						<el-table-column prop="productPack" label="" width="120">
						</el-table-column>
						<el-table-column prop="saleZone" label="" width="200">
						</el-table-column>
						<el-table-column prop="provinceName" label="" width="100">
						</el-table-column>
						<el-table-column prop="cityName" label="" width="100">
						</el-table-column>
						<el-table-column prop="scanPv" label="" width="100">
						</el-table-column>
						<el-table-column prop="effectScanPv" label="" width="100">
						</el-table-column>
						<el-table-column prop="scanUv" label="" width="100">
						</el-table-column>
						<el-table-column prop="newScanUv" label="" width="200">
						</el-table-column>
						<el-table-column prop="drawPv" label="" width="100">
						</el-table-column>
						<el-table-column prop="drawUv" label="" width="100">
						</el-table-column>
						<el-table-column prop="redpacketCost" label="" width="100">
						</el-table-column>
						<el-table-column prop="salePromotionCost" label="" width="100">
						</el-table-column>
						
					</el-table>
			</div>
		</div>
		
	</div>
</template>

<script>
	import '@/common/js/dateFormat.js';
	let userInfo=JSON.stringify(sessionStorage.getItem('cluser'));
	export default {
		data() {
			return {
				startDate: new Date(new Date().getTime() - 24 * 60 * 60 * 1000).Format('yyyy-MM-dd'),//
				startTime: '00:00',//
				endDate: new Date().Format('yyyy-MM-dd'),//
				endTime: '00:00',//
				conditionShow: true,//
				brand: [],//
				brandList: [],//
				spec: [],//
				specList: [],//
				packing:[],//
				packList:[{name:''},{name:''}],//
				area:[],//
				areaList:[],//
				pro:[],//
				proList:[],//,
				city:[],//
				cityList:[],//
				dialogTableVisible: false,//
				dialogTableVisible1: false,//
				loadValue:-1,//
				historyValue:-1,//
				loadList:[],//
				historyList:[],//
				tabActive:0,//tab
				tabList:['','','',''],//tab
				loading:true,//table
				tableData:[],//
        startDateTime: new Date().getTime() - 24 * 60 * 60 * 1000,
        endDateTime: new Date().getTime(),
			};
		},
		mounted() {
			this.getBrand();
			this.getArea();
			var that=this;
			var inter=setInterval(()=>{
				if(that.brand.length!=0){
					this.itemSearch(0);
					clearInterval(inter);
					inter=null;
				}
			},10)
			
		},
		computed:{
			transformStart(){
				var str=this.startDate+'_'+this.startTime.substr(0,2);
				return str;
			},
			transformEnd(){
				var str=this.endDate+'_'+this.endTime.substr(0,2);
				return str;
			},
			transformBrand(){
				var str='';
				if(this.brand.length!=0){
					str=this.brand.join(',')
				}else {
					str=''
				}
				return str;
			},
			transformSpec(){
				var str='';
				if(this.spec.length!=0){
					str=this.spec.join(',')
				}else {
					str=''
				}
				return str;
			},
			transformPack(){
				var str='';
				if(this.packing.length!=0){
					str=this.packing.join(',')
				}else {
					str=''
				}
				return str;
			},
			transformAreaShow(){
				if(this.area.length!=0){
					var c=this.area;
					let obj = [];
	      			c.forEach((val,i)=>{
	      				obj.push(this.areaList.find((item)=>{//userList
				          return item.code == val;//
				      }).name) ;
	      			})
	      			return obj.join(',')
				}else {
					return '';
				}
				
			    
			     console.log(obj);//namelabel
			},
			transformArea(){
				var str='';
				if(this.area.length!=0){
					str=this.area.join(',')
				}else {
					str=''
				}
				return str;
			},
			transformPro(){
				var str='';
				if(this.pro.length!=0){
					str=this.pro.join(',')
				}else {
					str=''
				}
				return str;
			},
			transformCity(){
				var str='';
				if(this.city.length!=0){
					str=this.city.join(',')
				}else {
					str=''
				}
				return str;
			},
			transformAreaCommon(){
				var that=this;
				return function(str){
					if(str.length!=0){
						let tempArr=str.split(',');
						let obj = [];
		      			tempArr.forEach((val)=>{
		      				obj.push(that.areaList.find((item)=>{//userList
					          return item.code == val;//
					      }).name) ;
		      			})
		      			return obj.join(',')
					}else {
						return '';
					}
				}
			}
		
		},
		methods: {
			selectStartDay(day) {//
			  this.startDateTime =  day.getTime()
				if(this.endDateTime<this.startDateTime){
				  alert("")
          this.startDate = new Date(new Date().getTime() - 24 * 60 * 60 * 1000).Format('yyyy-MM-dd')
					this.startDateTime =  new Date().getTime() - 24 * 60 * 60 * 1000
				}else{
          this.startDate = day.Format('yyyy-MM-dd');
				}
				this.selectTime()
			},
      selectTime(){
        if(this.startDate == this.endDate){
          let st = this.startTime.split(":")[0] - ""
          let et = this.endTime.split(":")[0] - ""
          if(st>et){
            alert("")
            this.startTime = "00:00"
            this.endTime = "00:00"
          }
        }
			},

			selectEndDay(day) {
			  this.endDateTime = day.getTime()
        if(this.endDateTime<this.startDateTime){
          alert("")
          this.endDate = new Date().Format('yyyy-MM-dd')
          this.endDateTime =  new Date().getTime()
        }else {
          this.endDate = day.Format('yyyy-MM-dd');
				}
        this.selectTime()
			},
			toggleShow() {//
				if(this.conditionShow) {
					this.conditionShow = false;
				} else {
					this.conditionShow = true;
				}
			},
			getBrand() {//
				var that = this;
				this.$request.post(
					'/record/public/getUserBrandByUserId', {

					},
					true,
					res => {
						var data = res || [];
						that.brandList = data;
						if(data.length != 0) {
							that.brand.push(data[0].name);
						}

					},
					err => {
						console.log(err)
					}
				)
			},
			getSpec(flag) {//
				var that = this;
				var params = that.brand.join(',');
				this.$request.post(
					'/record/statistics/getProductOfNoNuit', {
					productBrand:params
					},
					true,
					res => {
						var data = res || [];
						that.specList = data;

					},
					err => {
							console.log(err)
					}
				)

			},
			getArea(){//
				var that = this;
				this.$request.post(
					'/record/public/getUserSaleByUserId', {
					},
					true,
					res => {
						var data = res || [];
//						data=[{"code":"BJ","name":""},{"code":"DB","name":""},{"code":"DN","name":""},{"code":"GXYN","name":""},{"code":"HD","name":""},{"code":"HN","name":""},{"code":"HNHB","name":""},{"code":"NM","name":""},{"code":"O","name":""},{"code":"Q","name":""},{"code":"SD","name":""},{"code":"SN","name":""},{"code":"SX","name":""},{"code":"TJ","name":""},{"code":"XB","name":""},{"code":"XN","name":""}];
						that.areaList = data;

					},
					err => {
							console.log(err)
					}
				)
			},
			getPro(){//
				var that = this;
				if(that.area.length!=0){
					var params=that.area.join(',')
				}else {
					var params='';
				}
				this.$request.post(
					'/record/public/getUserProvByUserId', {
						saleZone:params
					},
					true,
					res => {
						var data = res || [];
						that.proList = data;

					},
					err => {
							console.log(err)
					}
				)
			},
			getCity(){//
				var that = this;
				if(that.pro.length!=0){
					var params=that.pro.join(',')
				}else {
					var params='';
				}
				this.$request.post(
					'/record/statistics/getCitysByProvince', {
						provinceName:params
					},
					true,
					res => {
						var data = res || [];
						that.cityList = data;

					},
					err => {
							console.log(err)
					}
				)
			},
			saveTpl(){//
				var that = this;
				var specName='';
				var proName='';
				if(that.transformSpec.length!=0){
					specName=that.transformSpec;
				}
				if(that.transformPro.length!=0){
					proName=that.transformPro;
				}
				this.$request.post(
					'/record/statistics/saveQueryRecord', {
						cityName:that.transformCity,
						ctime:new Date().getTime(),
						startTime:that.transformStart,
						endTime:that.transformEnd,
						opType:1,
						productBrand:that.transformBrand,
						productName:specName,
						productPack:that.transformPack,
						provinceName:proName,
						saleZone:that.transformArea
					},
					true,
					res => {
						if(res==1){
							this.$message({
								message: '',
								type: 'success'
							});
						}
					},
					err => {
						console.log(err)
					}
				)
			},
			load(){//
				var that=this;
				that.dialogTableVisible = true
				this.$request.post(
					'/record/statistics/getQueryRecord', {
						userId:userInfo.account,
						opType:1
					},
					true,
					res => {
						var data=res || [];
						that.loadList=data;
					},
					err => {
						console.log(err)
					}
				)
			},
			tplChange(){//
				this.dialogTableVisible = false;
				let index=this.loadValue;
				let arr=this.loadList;
				this.startDate=arr[index].startTime.substr(0,10);
				this.endDate=arr[index].endTime.substr(0,10);
				this.startTime=arr[index].startTime.substr(11,2)+':00';
				this.endTime=arr[index].endTime.substr(11,2)+':00';
				this.brand=arr[index].productBrand.split(',');
				this.spec=arr[index].productName==''?[]:arr[index].productName.split(',');
				this.packing=arr[index].productPack==''?[]:arr[index].productPack.split(',');
				this.area=arr[index].saleZone==''?[]:arr[index].saleZone.split(',');
				this.pro=arr[index].provinceName==''?[]:arr[index].provinceName.split(',');
				this.city=arr[index].cityName==''?[]:arr[index].cityName.split(',');
			},
			search(){//
				var that = this;
				var specName='';
				var proName='';
				if(that.transformSpec.length!=0){
					specName=that.transformSpec;
				}
				if(that.transformPro.length!=0){
					proName=that.transformPro;
				}
				this.$request.post(
					'/record/statistics/saveQueryRecord', {
						cityName:that.transformCity,
						ctime:new Date().getTime(),
						startTime:that.transformStart,
						endTime:that.transformEnd,
						opType:2,
						productBrand:that.transformBrand,
						productName:specName,
						productPack:that.transformPack,
						provinceName:proName,
						saleZone:that.transformArea,
						userId:userInfo.account
					},
					true,
					res => {
						if(res==1){
							console.log('')
						}
					},
					err => {
						console.log(err)
					}
				)
				this.itemSearch(this.tabActive)
			},
			getHistory(){//
				this.dialogTableVisible1 = true;
				var that=this;
				this.$request.post(
					'/record/statistics/getQueryRecord', {
						userId:userInfo.account,
						opType:2
					},
					true,
					res => {
						var data=res || [];
						that.historyList=data;
					},
					err => {
						console.log(err)
					}
				)
			},
			hisChange(){//
				this.dialogTableVisible1 = false;
				let index=this.historyValue;
				let arr=this.historyList;
				this.startDate=arr[index].startTime.substr(0,10);
				this.endDate=arr[index].endTime.substr(0,10);
				this.startTime=arr[index].startTime.substr(11,2)+':00';
				this.endTime=arr[index].endTime.substr(11,2)+':00';
				this.brand=arr[index].productBrand.split(',');
				this.spec=arr[index].productName==''?[]:arr[index].productName.split(',');
				this.packing=arr[index].productPack==''?[]:arr[index].productPack.split(',');
				this.area=arr[index].saleZone==''?[]:arr[index].saleZone.split(',');
				this.pro=arr[index].provinceName==''?[]:arr[index].provinceName.split(',');
				this.city=arr[index].cityName==''?[]:arr[index].cityName.split(',');
			},
			itemSearch(index){//
				this.loading=true;
				this.tabActive=index;
				let url='';
				var that=this;
				if(index==0){
					url='getMultiScanData'
				}else if(index==1){
					url='getMultiKPIData'
				}else if(index==2){
					url='getMultiCashData'
				}else if(index==3){
					url='getMultiGoodData'
				}				
				var specName='';
				var proName='';
				if(that.transformSpec.length!=0){
					specName=that.transformSpec;
				}
				if(that.transformPro.length!=0){
					proName=that.transformPro;
				}
				this.$request.post(
					'/record/statistics/'+url, {
						cityName:that.transformCity,
						ctime:new Date().getTime(),
						startTime:that.transformStart,
						endTime:that.transformEnd,
						opType:2,
						productBrand:that.transformBrand,
						productName:specName,
						productPack:that.transformPack,
						provinceName:proName,
						saleZone:that.transformArea,
						userId:userInfo.account
					},
					true,
					res => {
						var data=res || [];
						that.loading=false;
