			handleClose(tag){//
				if(tag==0){
					this.brand=[];
				}else if(tag==1){
					this.spec=[];
				}else if(tag==2){
					this.packing=[];
				}else if(tag==3){
					this.area=[];
				}else if(tag==4){
					this.pro=[];
				}else if(tag==5){
					this.city=[];
				}
