<template>

    <div>
        <el-card>
            <el-row :gutter="20">
                <el-col :span="16"><div class="grid-content bg-purple">/</div></el-col>
            </el-row>
            <div class="space"></div>
            <el-form :inline="true" :model="form" class="demo-form-inline">
                <el-row>
                    <el-form-item label="CODE" size="small" >
                        <el-input v-model="form.code" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item  size="small" label="SN/">
                        <el-input v-model="form.verifycode" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item size="small" label=":" >
                        <el-select v-model="form.type" placeholder="">
                            <el-option
                                    v-for="item in options"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-row>
                <el-form-item>
                    <el-row><el-button type="primary" v-on:click="queryData"></el-button></el-row>
                </el-form-item>
            </el-form>
            <el-table :data="listData" style="width: 100%" border>
                <el-table-column  label="" >
                    <template slot-scope="scope">
                        <a v-if="scope.row.lableStatus == 0" href="javascript:void(0)"  v-on:click="activeFn"></a>
                        <span v-else-if="scope.row.lableStatus == 1"></span>
                    </template>
                </el-table-column>
                <el-table-column  label="" >
                    <template slot-scope="scope">
                        {{['',''][scope.row.lableStatus]}}
                    </template>
                </el-table-column>
                <el-table-column label="">
                        <template slot-scope="scope">
                            {{['','',''][scope.row.type]}}
                        </template>
                </el-table-column>
                <el-table-column prop="code" label="CODE"></el-table-column>
                <el-table-column prop="verifycode" label=""></el-table-column>
                <el-table-column prop="sn" label="SN"></el-table-column>
                <el-table-column prop="batch" label=""></el-table-column>
                <el-table-column prop="subbatch" label=""></el-table-column>
                <el-table-column prop="productname" label=""></el-table-column>
                <el-table-column prop="count" label=""></el-table-column>
                <el-table-column prop="lastcreatetime" label=""></el-table-column>
                <el-table-column prop="firstscantime" label=""></el-table-column>

            </el-table>

        </el-card>
    </div>
</template>

<script>
    import draggable from 'vuedraggable'
    export default {
        components: {
            draggable
        },
        name: "QrcodeActive",
        data(){
            return{
                listData:[],
                options: [{
                    value: '1',
                    label: ''
                }, {
                    value: '2',
                    label: ''
                }],
                form:{
                    code:'',
                    verifycode:'',
                    type:'1'
                }
            }
        },
        created(){
        },
        methods:{
            getDataList(){
                this.$request.post(`/api/saotx/codeActivate/queryCode`,this.form,false,res => {
                    console.log(res.data);
                        if (res.ret === '200000') {
                            this.listData.push( res.data);
                        }
                    }
                ),
                    err => {
                        console.log(err)
                    }
            },

            queryData(){
                if(this.form.code==''){
                    this.$message.error("code");
                    return;
                }
                if(this.form.verifycode==''){
                    this.$message.error("");
                    return;
                }
                this.getDataList();
            },
            activeFn(){
                this.$request.post(`/api/saotx/codeActivate/updateDate`,this.form,false,res => {
                        console.log(res.data);
                        if (res.ret === '200000') {
                            this.getDataList();
                        }
                    }
                ),
                    err => {
                        console.log(err)
                    }

            }
        }
    }
</script>

<style scoped>

</style>



